import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { EngineeringConvertersComponent } from './engineering-converters.component';

const routes: Routes = [{ path: '', component: EngineeringConvertersComponent }];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EngineeringConvertersRoutingModule { }
